@import './../styles/stylesheet.scss';


.body{
    background: $bg;
    height: 100vh;
    text-align: center;

    &__title{
        font-family: $Title;
        font-size: 2.2rem;
        color: $main-blue;
        margin:0;
        //padding:10px;
        display:inline-block;
    } 
    &__subtitle{
        font-family: $Header;
        font-size: 1.5rem;
        color: $secondary-blue;
        margin:0;
        padding-top: 50px;
    }
    &__bio{
        font-family: $Body_Text;
        font-size: 1rem;
        color: $text-black;
        padding: 10px;
        height:fit-content;
        width: 300px;
        background: white;
        border: 0.25vmin solid $light-grey;
        border-radius: 25px;
    }
    &__genreButton{
        width: 100px;
        height: 100px;
        text-decoration: none;
        background-color: $tertiary-blue;
        border: 0.25vmin solid $tertiary-blue;
        border-radius: $buttonRadius;
        font-family: $Header;
        font-size: 1rem;
        color: white;
    }
}

.card{
    border: 0.25vmin solid $light-grey;
    background-color: white;
    border-radius: 25%; 

    &__img{ 
        width: 200px;
        height: 200px;
        padding-top: 10px;
        object-fit:fill;
        border-radius: 25%;  
    }
}

.grid{
    display: inline-grid;
    grid-template-columns: auto auto;
    gap: 70px;
    padding-top: 20px;
    padding-left: 200px;
    padding-right: 200px;
    height:200px;
    place-items: center;
}

.secondaryGrid{
    display: inline-grid;
    grid-template-columns: auto auto;
    gap: 40px;
    padding-top: 20px;
    padding-left: 200px;
    padding-right: 200px;
    height:100px;
    place-items: center;
}

.genreGrid{
    display: inline-grid;
    grid-template-columns: auto auto auto auto auto;
    gap: 10px;
    padding: 20px;
    height:100px; 
    background: white;
    border-radius: 25px;
    border: 0.25vmin solid $light-grey;
    overflow:scroll;
    overflow-x:hidden;
}