@import './../styles/stylesheet.scss';

.header{
    background: $bg;
    height: $headerHeight;
    padding: 0 1.5rem;
    border-bottom: 2px solid $light-grey;
    @include breakpoint(md) {
        padding: 0 3rem;
    }
    transition: 0.3s ease all;

    &__content {
        overflow: hidden;
        color: $text-black;
        margin: 0 auto;
        max-width: 1920px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        &__logo{
            font-family: $Title;
            font-size: 2.2rem;
            color: $main-blue;
        }
        &__nav {
            top: 0;
            right: 100%;
            bottom: 0;
            width: 100%;
            height: 100vh;
            position: fixed;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            background: rgba($bg, 0.9);
            backdrop-filter: blur(2px);
            transform: translate(0);
            transition: 0.3s ease transform;
            padding-right: 6rem;

            @include breakpoint(md) {
                transform: none;
                flex-direction: row;
                background: transparent;
                width: auto;
                height: 100%;
                position: static;
            }
            ul {
                list-style: none;
                padding: 0;
                display: flex;
                flex-direction: column;
                margin-bottom: $spacing-lg;
                @include breakpoint(md) {
                    flex-direction: row;
                    align-items: center;
                    margin-bottom: 0;
                    margin-right: calc(0.5rem + #{$spacing-md});
                }
                li {
                    &:not(:last-child) {
                        margin-bottom: $spacing-lg;
                        @include breakpoint(md) {
                            margin-bottom: 0;
                            margin-right: $spacing-md;
                        }
                    }
                    text-decoration: none;
                    color: $text-black;
                    font-family: $Header;
                    font-size: 1.4rem;
                    padding: 0.75rem 1.5rem;
                    border-radius: $borderRadius;
                    transition: 0.3s ease all;

                    &:hover {
                        background: rgba($light-yellow, 0.5);
                    }
                }
            }

        }
    
    }

}
